form{
    margin-top: 3em;
}
form input[name="city"],
form input[name="country"] {
  background-color: transparent;
  color: white;
  border: none;
  border-bottom: 2px solid whitesmoke;
  border-radius: 0;
}
form input:focus{
    background-color: transparent !important;
    box-shadow: none !important;
}
 